	module.exports = {"data":{"markdownRemark":{"html":"<h2>Empathy Maps</h2>\n<!-- The article goes here, in GitHub-flavored Markdown. Feel free to add YouTube videos, images, and CodePen/JSBin embeds  -->\n<p><img src=\"https://www.solutionsiq.com/images/Empathy-Map-No-Stickies.png\" alt=\"A diagram of the empathy map including a human face at the centre, surrounded by the phrases &#x22;Think &#x26; Feel?&#x22; &#x22;See?&#x22; &#x22;Say &#x26; Do?&#x22; &#x22;Hear?&#x22; &#x22;Pain&#x22; and &#x22;Gain&#x22;.\"></p>\n<p>Empathy mapping is a tool used to anticipate the thoughts, emotions, needs, and reactions of an interface user.  They can be used along with user personas in User Experience Research at the beginning of the design process, in order to determine what problems the user is facing, and how the interface design can help resolve them.</p>\n<p>Empathy maps work best when researchers make them after conducting user interviews.  Empathy mapping sessions benefit from a diverse range of team members: stakeholders, customer support leads, vendors, product developers, salespeople, and copywriters.</p>\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->","fields":{"slug":"/user-experience-research/empathy-maps/"},"frontmatter":{"title":"Empathy Maps"}}},"pathContext":{"slug":"/user-experience-research/empathy-maps/"}}

